*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
main{
  display: flex;
  flex-direction: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  font-size: 2rem;
}
header{
  background-color: blue;
  color: white;
  text-align: center;
  top:0;
  width: 100%;
}

footer
{
  background-color: blue;
  color: white;
  text-align: center; 
  position: fixed;
  bottom: 0;
  width: 100%;
}

.item 
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.5rem;
  padding: 0.5rem;
  background-color: #f4f4f4;
  border-radius: 5px;
  width: 400px;

}

input[type="checkbox"]
{
  margin-right: 0.5rem;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

button
{
  background-color: red;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
}

